* {
  padding: 0;
  margin: 0;
}

html, body {
  font-family: "Fira Sans";
  overflow-x: hidden;
  background: url("../img/texture.png");
  margin-top: -20px;
}

.container--two {
  box-sizing: border-box;
  width: 100% !important;
  height: 100%;
  max-width: 1070px;
  position: relative;
  margin: 0 auto;
}

.container {
  box-sizing: border-box;
  width: 100% !important;
  height: 100%;
  max-width: 945px;
  position: relative;
  margin: 0 auto;
}

.row {
  height: 100%;
}

a {
  font-size: 16px;
  text-decoration: none;
  transition: all 0.3s;
  color: #000000;
}

p {
  font-size: 26px;
}

h1, h2 {
  font-family: 'Literaturnaya';
}

.btn {
  padding: 15px 45px;
  font-family: 'Literaturnaya';
  font-size: 16px;
  font-weight: bold;
  letter-spacing: 0.9px;
  color: #3a3628;
  border-radius: 6px;
  background-color: #dbd273;
  box-shadow: 0 2px 0 0 #78985c;
  border-bottom: none;
  display: inline-block;
  text-transform: uppercase;
  text-align: center;
}

.separation {
  width: 100%;
  position: relative;
  z-index: 10;
}

.separation img {
  width: 100%;
  margin-top: -2px;
}

.heading h2, .heading h3 {
  font-family: 'Literaturnaya';
  font-size: 44px;
  font-weight: bold;
  line-height: 1.14;
  letter-spacing: 1.8px;
  color: #3a3628;
  text-shadow: 2px 2px 0 #d5cba4;
  padding-top: 45px;
  text-transform: uppercase;
}

.heading p {
  font-family: 'Fira Sans';
  font-size: 26px;
  line-height: 1.54;
  letter-spacing: 1px;
  color: #3a3628;
  padding-top: 45px;
  padding-bottom: 40px;
}

.heading p span {
  font-weight: bold;
}

.heading--conditions h2 {
  padding-top: 0px;
}

.heading--partners h2 {
  padding-bottom: 65px;
  padding-top: 0px;
}

.heading--description h2 {
  color: #ffffff;
  text-shadow: 2px 2px 0 #45402c;
}

.heading--description h3 {
  padding-top: 0;
  color: #ffffff;
  text-shadow: 2px 2px 0 #45402c;
}

.info__block p {
  padding-top: 15px;
  font-size: 22px;
  line-height: 1.36;
  letter-spacing: 0.5px;
  color: #3a3628;
}

.info__block p:nth-child(3) {
  font-size: 16px;
  line-height: 1.25;
  color: #7c7c7c;
  padding-top: 0;
}

.advantages__block {
  width: 100%;
  height: 100%;
  position: relative;
}

.advantages__block img {
  position: absolute;
}

.advantages__block--regulation img {
  left: 0;
  bottom: 0px;
}

.advantages__block--credit img {
  left: -60%;
  bottom: -19px;
}

.advantages__block__text {
  padding-top: 110px;
  padding-bottom: 150px;
}

.advantages__block__text h2 {
  font-size: 44px;
  font-weight: bold;
  letter-spacing: 1.8px;
  text-align: left;
  color: #ffffff;
  text-shadow: 2px 2px 0 #45402c;
  max-width: 440px;
  text-transform: uppercase;
  padding-bottom: 40px;
}

.advantages__block__text p {
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: 0.9px;
  color: #f8f5d6;
  padding-bottom: 35px;
}

.advantages__block__text--documentation h2 {
  max-width: 570px;
}

.advantages__block__text--documentation p {
  padding-bottom: 0;
  line-height: 8px;
}

.advantages__block__text--documentation p:nth-child(6) {
  padding-bottom: 45px;
}

.advantages__block__text--documentation p:nth-child(2) {
  line-height: 1.25;
}

form input {
  max-width: 165px;
  text-align: center;
  border-bottom: 2px solid #a0b77b;
  margin-right: 30px;
  font-size: 20px;
  letter-spacing: 0.5px;
  color: #7c7c7c;
  background: transparent;
}

form input[type="submit"] {
  width: auto;
  max-width: 100%;
  background-color: #dbd273;
}

form input[type="checkbox"] {
  display: none;
}

form label {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
  font-size: 12px;
  letter-spacing: 0.7px;
  color: #797979;
  padding-bottom: 40px;
}

form input[type="checkbox"] + label::before {
  content: '';
  font-family: 'FontAwesome';
  width: 16px;
  height: 16px;
  border: 1px solid #a0b77b;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  right: 20px;
  color: #a0b77b;
}

form input[type="checkbox"]:checked + label:before {
  content: '\f00c';
}

.header {
  padding: 20px 30px;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 10;
}

.header__block {
  display: inline-flex;
  align-items: center;
}

.header__block i {
  display: block;
  padding-right: 70px;
  font-size: 20px;
}

.header__block__calculator {
  display: inline-flex;
  align-items: center;
  font-family: 'Literaturnaya';
  font-size: 26px;
  color: #494745;
}

.header__block__calculator img {
  position: relative;
  top: -4px;
  padding-right: 20px;
}

.header__block__phone {
  font-family: 'Literaturnaya';
  font-size: 26px;
  font-weight: bold;
  color: #494745;
  display: inline-flex;
  align-items: center;
}

.header__block__phone img {
  position: relative;
  top: -2px;
  padding-right: 20px;
}

.intro {
  padding-top: 80px;
  z-index: 9;
  position: relative;
}

.intro:before {
  content: '';
  width: 100%;
  height: 110%;
  position: absolute;
  top: 0;
  background: url("../img/intro/bg-city.png") no-repeat;
  background-size: 100% 100%;
}

.intro h1 {
  font-size: 44px;
  font-weight: bold;
  line-height: 1.23;
  letter-spacing: 1.8px;
  color: #3a3628;
  text-shadow: 2px 2px 0 #d5cba4;
  max-width: 700px;
  width: 100%;
  margin: 0 auto;
  padding: 25px 0;
}

.intro p {
  font-size: 26px;
  line-height: 1.54;
  letter-spacing: 1px;
  color: #3a3628;
}

.intro p:nth-child(4) {
  padding-bottom: 40px;
}

.regulation {
  background: url("../img/regulation.png");
  background-size: 100% 100%;
}

.step {
  background: url("../img/step.png") no-repeat;
  background-size: contain;
  margin-top: -20px;
}

.credit {
  background: url("../img/credit.png");
  background-size: 100% 100%;
}

.calculator {
  background: url("../img/step.png") no-repeat;
  background-size: contain;
  margin-top: -20px;
}

.deposit {
  position: relative;
}

.deposit:before {
  content: '';
  position: absolute;
  bottom: -20px;
  width: 100%;
  height: 100%;
  background: url("../img/step.png") no-repeat;
  background-size: contain;
  transform: rotate(180deg);
}

.documentation {
  background: url("../img/documentation.png");
  background-size: 100% 100%;
}

.benefit {
  background: url("../img/step.png") no-repeat;
  background-size: contain;
  margin-top: -20px;
}

.partners {
  position: relative;
}

.partners:before {
  content: '';
  position: absolute;
  bottom: -20px;
  width: 100%;
  height: 100%;
  background: url("../img/step.png") no-repeat;
  background-size: contain;
  transform: rotate(180deg);
}

.partners__content {
  display: inline-flex;
  align-items: center;
}

.partners__content__block {
  padding-right: 50px;
  padding-bottom: 45px;
}

.partners__content__block a {
  width: 100%;
  height: 100%;
}

.description {
  background: url("../img/description.png") no-repeat;
  padding-bottom: 100px;
}

.description__text p {
  font-size: 22px;
  line-height: 1.36;
  letter-spacing: 1.1px;
  color: #f8f5d6;
  padding-bottom: 30px;
}
