.partners {
  position: relative;
  &:before {
    content: '';
    position: absolute;
    bottom: -20px;
    width: 100%;
    height: 100%;
    background: url('../img/step.png') no-repeat;
    background-size: contain;
    transform: rotate(180deg); }
  &__content {
    display: inline-flex;
    align-items: center;
    &__block {
      padding-right: 50px;
      padding-bottom: 45px;
      a {
        width: 100%;
        height: 100%; } } } }
